import { Point, Size, UnitInterval } from "./LifeBoard";
import retinaCanvasScaling from "./retinaCanvasScaling";
import { Board } from "./lifeBoardCalculator";
import { RoughCanvas } from "roughjs/bin/canvas";
import rough from "roughjs/bin/rough";
import { Drawable } from "roughjs/bin/core";

export type Draw = {
  initCanvas: (
    canvas: HTMLCanvasElement,
    ctx: CanvasRenderingContext2D,
    lineWidth: number,
    canvasSizePx: Size,
    board: Board
  ) => void;
  drawUnit: (
    canvas: HTMLCanvasElement,
    ctx: CanvasRenderingContext2D,
    board: Board,
    unit: number,
    color: string
  ) => void;
  drawInterval: (
    canvas: HTMLCanvasElement,
    ctx: CanvasRenderingContext2D,
    board: Board,
    interval: UnitInterval
  ) => void;
};

const initCanvas = (
  canvas: HTMLCanvasElement,
  ctx: CanvasRenderingContext2D,
  lineWidth: number,
  canvasSizePx: Size,
  board: Board
) => {
  retinaCanvasScaling(canvas, ctx, canvasSizePx);
  ctx.lineWidth = lineWidth;
};

const drawUnitRect = (
  canvas: HTMLCanvasElement,
  ctx: CanvasRenderingContext2D,
  board: Board,
  unit: number,
  color: string
) => {
  ctx.strokeStyle = color;
  const center = board.unitCenter(unit);
  const size = board.scaledSize(board.unitSize, 0.9, 0.9);
  ctx.strokeRect(center.x - size.dx / 2, center.y - size.dy / 2, size.dx, size.dy);
};

const fillUnitRect = (
  canvas: HTMLCanvasElement,
  ctx: CanvasRenderingContext2D,
  center: Point,
  size: Size,
  color: string,
  from: number,
  to: number
) => {
  ctx.fillStyle = color;
  ctx.fillRect(center.x - size.dx / 2 + from * size.dx, center.y - size.dy / 2, size.dx * (to - from), size.dy);
};

const drawIntervalRect = (
  canvas: HTMLCanvasElement,
  ctx: CanvasRenderingContext2D,
  board: Board,
  interval: UnitInterval
) => {
  for (let unit = interval.A; unit <= interval.B; ++unit) {
    const center = board.unitCenter(unit);
    const size = board.scaledSize(board.unitSize, 0.9, 0.9);
    fillUnitRect(
      canvas,
      ctx,
      center,
      size,
      interval.color,
      unit === interval.A ? interval.from : 0,
      unit === interval.B ? interval.to : 1
    );
  }
};

export const normalDraw: Draw = {
  initCanvas,
  drawUnit: drawUnitRect,
  drawInterval: drawIntervalRect,
};

