let rc: RoughCanvas;
let gridDrawables: Array<Drawable> = [];
const initCanvasRough = (
  canvas: HTMLCanvasElement,
  ctx: CanvasRenderingContext2D,
  lineWidth: number,
  canvasSizePx: Size,
  board: Board
) => {
  retinaCanvasScaling(canvas, ctx, canvasSizePx);
  ctx.lineWidth = lineWidth;
  rc = rough.canvas(canvas, {
    options: {
      roughness: 2,
      bowing: 3,
      seed: Math.floor(Math.random() * 100),
      fillWeight: 1,
      // seed: 2,
      stroke: `${lineWidth}px`,
    },
  });
  for (let unit = 0; unit < board.row * board.column; ++unit) {
    const center = board.unitCenter(unit);
    const size = board.scaledSize(board.unitSize, 0.9, 0.9);
    gridDrawables.push(rc.generator.rectangle(center.x - size.dx / 2, center.y - size.dy / 2, size.dx, size.dy, {}));
  }
};

const drawUnitRough = (
  canvas: HTMLCanvasElement,
  ctx: CanvasRenderingContext2D,
  board: Board,
  unit: number,
  color: string
) => {
  // const rc: RoughCanvas = rough.canvas(canvas, {
  //   options: {
  //     roughness: 2,
  //     bowing: 3,
  //     seed: Math.floor(Math.random() * 100),
  //     fillWeight: 1,
  //     stroke: `20px`,
  //   },
  // });
  // const center = board.unitCenter(unit);
  // const size = board.scaledSize(board.unitSize, 0.9, 0.9);
  // rc.draw(rc.rectangle(center.x - size.dx / 2, center.y - size.dy / 2, size.dx, size.dy));
  rc.draw(gridDrawables[unit]);
};

const fillUnitRough = (
  canvas: HTMLCanvasElement,
  ctx: CanvasRenderingContext2D,
  center: Point,
  size: Size,
  color: string,
  from: number,
  to: number
) => {
  const rc: RoughCanvas = rough.canvas(canvas, {
    options: {
      roughness: 2,
      bowing: 3,
      seed: Math.floor(Math.random() * 100),
      fillWeight: 1,
    },
  });
  rc.draw(
    rc.rectangle(center.x - size.dx / 2 + from * size.dx, center.y - size.dy / 2, size.dx * (to - from), size.dy, {
      fill: color,
      fillStyle: "cross-hatch",
    })
  );
  // rc.draw(gridDrawables[unit]);
};

const drawIntervalRough = (
  canvas: HTMLCanvasElement,
  ctx: CanvasRenderingContext2D,
  board: Board,
  interval: UnitInterval
) => {
  for (let unit = interval.A; unit <= interval.B; ++unit) {
    const center = board.unitCenter(unit);
    const size = board.scaledSize(board.unitSize, 0.9, 0.9);
    fillUnitRough(
      canvas,
      ctx,
      center,
      size,
      interval.color,
      unit === interval.A ? interval.from : 0,
      unit === interval.B ? interval.to : 1
    );
  }
};

export const roughdDraw: Draw = {
  initCanvas: initCanvasRough,
  drawUnit: drawUnitRough,
  drawInterval: drawIntervalRough,
};
